<template>
	<div id="finance-getcash">
		<public-header title="申请提现"></public-header>
		<div class="content">
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">
						账户可用余额:
						<span class="c-primary margin-5-l">¥{{deliveryer.credit2}}</span>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t padding-15 border-0px">
				<p class="font-14 margin-10-b">提现金额</p>
				<input v-model.number="get_fee" type="text" class="getcash-num" placeholder="请输入提现金额">
				<div class="getcash-rule">
					<p>最低提现金额为{{fee_getcash.get_cash_fee_limit > 0 ? fee_getcash.get_cash_fee_limit : '0'}}元</p>
					<p>提现费率为{{fee_getcash.get_cash_fee_rate > 0 ? fee_getcash.get_cash_fee_rate : '0'}}%，最低收取{{fee_getcash.get_cash_fee_min > 0 ? fee_getcash.get_cash_fee_min : '0'}}元，最高收取{{fee_getcash.get_cash_fee_max > 0 ? fee_getcash.get_cash_fee_max : '0'}}元</p>
				</div>
			</van-cell-group>
			<van-radio-group v-model="channel" v-if="config && config.type">
				<p class="getcash-type">提现方式</p>
				<van-cell-group class="border-0px">
					<van-cell title="微信提现" clickable @click="channel = 'weixin'">
						<van-radio name="weixin" />
					</van-cell>
					<van-cell title="支付宝提现" clickable @click="channel = 'alipay'" v-if="config.type.alipay == 1">
						<van-radio name="alipay" />
					</van-cell>
					<van-cell title="银行卡提现" clickable @click="channel = 'bank'" v-if="config.type.bank == 1">
						<van-radio name="bank" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<div class="padding-15">
				<van-button size="normal" :disabled="!islegal" block class="font-16 bg-primary" @click="onSubmit">提现</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				channel: 'weixin',
				deliveryer: {},
				fee_getcash: {},
				get_fee: '',
				showPreLoading: true,
				islegal: false,
				config: {}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'delivery/finance/getcash/index',
					autoAssign: true,
					variable: 'deliveryer',
					success: function(res) {
						that.islegal = true;
						that.fee_getcash = res.deliveryer.fee_getcash;
						that.config = res.config;
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				let get_fee = parseFloat(that.get_fee);
				if(get_fee <= 0 || !get_fee) {
					that.util.$toast('提现金额有误');
					return false;
				}
				if(get_fee > that.deliveryer.credit2) {
					that.util.$toast('提现金额不能大于账户可用余额');
					return false;
				}
				if(get_fee < that.fee_getcash.get_cash_fee_limit) {
					that.util.toast('提现金额不能小于' + that.fee_getcash.get_cash_fee_limit + '元');
					return false;
				}
				that.islegal = false;
				setData({
					vue: that,
					url: 'delivery/finance/getcash/submit',
					data: {
						get_fee: get_fee,
						channel: that.channel
					},
					success: function(res) {
						var id = res.id;
						var url = that.util.getUrl({path: '/pages/finance/getcashDetail', query:{id: id}});
						that.util.$toast(res.message, url, 1000);
					},
					fail: (res) => {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#finance-getcash .getcash-num{
		border:1px solid #e7e7e7;
		border-radius:3px;
		font-size:14px;
		padding:1px 0 1px 6px;
		height: 34px;
		width: 100%;
		display: block;
	}
	#finance-getcash .getcash-rule p{
		color: #999;
		position: relative;
		padding-left: 10px;
		font-size: 14px;
		margin-top: 10px;
	}
	#finance-getcash .getcash-rule p:before{
		content: "";
		width: 5px;
		height: 5px;
		border-radius: 50%;
		position: absolute;
		left: 0;
		background: #ccc;
		top: 50%;
		margin-top: -2.5px;
	}
	#finance-getcash .getcash-type{
		font-size: 14px;
		padding: 0 15px;
		background-color: #fff;
	}
	#finance-getcash .van-cell__title{
		color: #999;
	}
	#finance-getcash .van-radio .van-icon-checked{
		color: #0EC3B3;
	}
</style>


